import { Box } from "@mui/material";
import { useContext } from "react";
import { Context } from "../..";
import Document from "./Document";
import { observer } from "mobx-react-lite";
import styles from "./style.module.css";

const DocumentContainer = (props) => {
	const { store } = useContext(Context);
	const { doc } = store;
	const { isLoading } = doc;

	const urlParts = new URL(window.location.href);
	const queryParamID = Number(urlParts.pathname.split('/').pop());
	const docID = !isNaN(queryParamID) ? queryParamID : null;

	const currentDoc = doc.getDocument(docID);

	const handleChangeTitle = (v) => {
		doc.setTitle(currentDoc, v);
	}

	const handleChangeDescription = (v) => {
		console.log(currentDoc, v)
		doc.setDescription(currentDoc, v);
	}

	const handleSendData = () => {
		doc.setLoading(true);
	}

	const handleAiFixText = async (txt) => {
		const div = document.createElement("div");
		div.innerHTML = currentDoc.description || "";
		
		const sendText = div.innerText;
		console.log(sendText)
		div.remove();



		const respFixTxt = await fetch('http://176.123.163.239:100/corrections-api/correct-text-long-ai-powered', {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json'
			},
			body: JSON.stringify({ text: sendText })
		});

		const fixTxt = await respFixTxt.json();
		doc.setDescription(currentDoc, fixTxt[0]);
	}

	return (
		<Box className={styles.container}>
			<Document data={currentDoc} loading={isLoading}
				sendData={handleSendData}
				aiFixText={handleAiFixText}
				onChangeTitle={handleChangeTitle}
				onChangeDescription={handleChangeDescription} />
		</Box>
	)
}

export default observer(DocumentContainer);