import { Box, FormControl, IconButton, InputAdornment, TextField, Typography } from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import styles from "./styles.module.css";
import { useContext, useState } from "react";
import { Context } from "../..";
import { observer } from "mobx-react-lite";
import { LoadingButton } from "@mui/lab";

const Auth = () => {
	const [showPassword, setShowPassword] = useState(false);
	const [login, setLogin] = useState("");
	const [password, setPassword] = useState("");

	const {store} = useContext(Context);
	const {auth} = store;
	const {isLoading = false} = auth;

	const handleClickShowPassword = () => setShowPassword(!showPassword);

	const signIn = async ()=>{
		await auth.login();
	}

	return (
		<Box sx={{ boxShadow: 1 }} className={styles.container}>
			<Typography className={styles.title} variant="h5" gutterBottom>  </Typography>
			<FormControl sx={{mb:1}} fullWidth>
				<TextField name="login" autoComplete="login" label=" " variant="standard" onChange={e => setLogin(e.target.value)} value={login} />
			</FormControl>
			<FormControl sx={{mb:5}} fullWidth>
				<TextField
					name="password"
					autoComplete="password"
					label=''
					value={password}
					variant="standard"
					onChange={e => setPassword(e.target.value)}
					type={showPassword ? "text" : "password"}
					InputProps={{
						endAdornment: (
							<InputAdornment position="end">
								<IconButton
									aria-label=" "
									onClick={handleClickShowPassword}
								>{showPassword ? <Visibility /> : <VisibilityOff />}
								</IconButton>
							</InputAdornment>
						)
					}}
				/>
			</FormControl>
			<LoadingButton className={styles.btnSubmit} variant="contained" type="submit" loading={isLoading} onClick={signIn}></LoadingButton>
		</Box>
	);
}
export default observer(Auth);