import { createTheme } from '@mui/material/styles';


const theme = createTheme({
	palette: {
		mode: 'light',
		primary: {
			main: '#ec6608',
			dark: '#cf5d0c',
		},
		secondary: {
			main: '#673ab7',
		},
	},
	typography: {
		fontFamily: ['GothamPro', '-apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Open Sans', 'Helvetica Neue', 'sans-serif'].join(','),
		h6: {
			fontWeight: 400,
		},
	},
	components: {
		// Name of the component
		MuiDataGrid: {
			styleOverrides: {
				// Name of the slot
				root: {
					// Some CSS
					whiteSpace: "normal",
					wordWrap: "break-word",
				},
				cell: {
					whiteSpace: "normal",
					wordWrap: "break-word",
				}
			},
		},

		MuiInputBase: {
			styleOverrides: {
				"root": {
					"&.Mui-disabled": {
						"background": "#f0f0f0"
					}
				}

			}
		}
	}
},
);

export default theme;