import React, { useEffect } from 'react';
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';
import StopCircleIcon from '@mui/icons-material/StopCircle';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import styles from "./style.module.css";

const Recorder = (props) => {
	const { className:customClassName="", value = "", setValue = () => { } } = props;
	const {
		transcript,
		listening,
		browserSupportsSpeechRecognition
	} = useSpeechRecognition();

	useEffect(() => {
		setValue(transcript);
	}, [transcript]);

	if (!browserSupportsSpeechRecognition) {
		return <span>    </span>;
	}

	const recordingOptions = { continuous: true };

	const handleStartRecording = () => {
		SpeechRecognition.startListening(recordingOptions);
	}

	const handleStopRecording = () => {
		SpeechRecognition.stopListening();
	}

	return (
		<div className={customClassName}>
			{listening ? <StopCircleIcon className={styles["active_recording"]} onClick={handleStopRecording} /> : <KeyboardVoiceIcon onClick={handleStartRecording} />}
		</div>
	);
};
export default Recorder;