import { action, makeAutoObservable, observable, runInAction } from "mobx";
import { makePersistable } from "mobx-persist-store";
import DataService from "../services/DocService";
import AiService from "../services/AiService";

class DocStore {
	isLoading = false;

	documents = [];
	originalDocuments = [];

	title = "";
	description = "";

	constructor(initialData = {title:this.title, description:this.description}) {
		makeAutoObservable(this, {
			isLoading: observable,
			documents: observable,
			originalDocuments: observable,
			title: observable,
			description: observable,
			getDocument:action,
			setLoading: action,
			setDocuments: action,
			setDocument: action,
			setDocumentTitle: action,
			setDocumentDescription: action
		});

		makePersistable(this, {
			name: 'DocStore',
			storage: typeof window !== "undefined" ? window.localStorage : undefined,
			properties: [
				'documents',
				'originalDocuments',
			]
		});
		
		this.title = initialData.title
		this.description = initialData.description
	}

	getDocument(id){
		return this.documents.find((d)=> d.id === id) || this;
	}

	setLoading(loading) {
		this.isLoading = loading;
	}

	setDocuments(documents) {
		this.documents = documents;
	}

	setOriginalDocuments(documents) {
		this.originalDocuments = documents;
	}

	setTitle(d = this, v) {
		console.log(d, v)
		d.title = v;
	}

	setDescription(d = this, v) {
		console.log(d,v)
		d.description = v;
	}

	

	async getDocuments() {
		this.setLoading(true);
		try {
			const respDocuments = await DataService.getDocuments();
			runInAction(() => {
				this.setDocuments(respDocuments.data);
				this.setOriginalDocuments(respDocuments.data);
			});

		}
		catch (e) {
			console.error(e);

		}
		finally {
			runInAction(() => {
				this.setLoading(false);
			});

		}
	}

	async deleteDocument(id) {
		this.setLoading(true);
		try {
			await DataService.deleteDocument(id);
			runInAction(() => {
				const newDocs = this.documents.filter((d) => d.id !== id);
				const newOriginalDocs = this.originalDocuments.filter((d) => d.id !== id);
				this.setDocuments(newDocs);
				this.setOriginalDocuments(newOriginalDocs);
			});

		}
		catch (e) {
			console.error(e);

		}
		finally {
			runInAction(() => {
				this.setLoading(false);
			});

		}
	}

	async AiFixText(txt){
		this.setLoading(true);
		try {
			await AiService.fixText(txt);
		}
		catch (e) {
			console.error(e);
			console.log(e.message)

		}
		finally {
			runInAction(() => {
				this.setLoading(false);
			});

		}
	}
}

export default DocStore;