import { Box, IconButton, InputAdornment, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import { useContext, useEffect, useState } from "react";
import { Context } from "../..";
import styles from "./styles.module.css";
import { Link } from "react-router-dom";
import { observer } from "mobx-react-lite";

const Documents = () => {
	const [search, setSearch] = useState("");
	const { store } = useContext(Context);
	const { doc } = store;
	const { documents, originalDocuments } = doc;

	const fetchDocuments = async () => {
		await doc.getDocuments();
	}

	const handleDeleteDoc = async (d)=>{
		await doc.deleteDocument(d.id);
	}

	useEffect(() => {
		fetchDocuments();
	}, []);

	useEffect(() => {
		console.log(originalDocuments)
		if (!search) doc.setDocuments(originalDocuments);

		doc.setDocuments(originalDocuments.filter(d => d.title.includes(search) || d.created_at.includes(search)));
	}, [search]);

	return (
		<Box>
			<Typography className={styles.title} variant="h5" gutterBottom> </Typography>
			<TableContainer component={Paper}>
				<TextField sx={{ width: "100%" }} variant="standard" value={search} onChange={(e) => setSearch(e.target.value)}
					placeholder="   "
					InputProps={{
						className: styles["search-input"],
						endAdornment: (
							<InputAdornment position="end">
								<IconButton
									aria-label=""
								><SearchIcon />
								</IconButton>
							</InputAdornment>
						)
					}}
				/>
				<Table sx={{ minWidth: 650 }} aria-label="simple table">
					<TableHead>
						<TableRow>
							<TableCell></TableCell>
							<TableCell> </TableCell>
							<TableCell width={"10%"} align="center"></TableCell>
						</TableRow>
					</TableHead>
					<TableBody>
						{documents.map((row) => {
							const localDate = row.created_at ? new Date(row.created_at).toLocaleDateString() : "-";
							return (
								<TableRow key={row.id} sx={{ '&:last-child td, &:last-child th': { border: 0 } }} >
									<TableCell component="th" scope="row">
										<Link to={`/document/${row.id}`}>{row.title}</Link>
									</TableCell>
									<TableCell>{localDate}</TableCell>
									<TableCell width={"10%"} align="center">
										<IconButton
											aria-label=""
											onClick={()=>handleDeleteDoc(row)}
										><DeleteOutlineIcon className={styles["trash-icon"]}  />
										</IconButton>
									</TableCell>
								</TableRow>
							)
						})
						}
					</TableBody>
				</Table>
			</TableContainer>
		</Box>
	)
}

export default observer(Documents);