import { Editor } from '@tinymce/tinymce-react';
import { useRef } from 'react';
import theme from "../../data/theme";

export default function RichTextEditor(props) {
	const { initValue = "", onChange =()=>{}, onEditorChange = ()=>{} } = props;
	const editorRef = useRef(null);

	const scrTinyMceScript = `${process.env.PUBLIC_URL}/tinymce/tinymce.min.js`;

	return (
		<Editor
			tinymceScriptSrc={scrTinyMceScript}
			value={initValue}
			onChange={onChange}
			onEditorChange={onEditorChange}
			scriptLoading={
				{
					async:true
				}
			}
			onInit={(evt, editor) => editorRef.current = editor}
			init={{
				branding: false,
				height: 300,
				skin: "oxide_orange",
				menubar: false,
				language: "ru",
				directionality: 'ltr',
				language_url: process.env.PUBLIC_URL + "/tinymce/langs/ru.js",
				plugins: [
					'advlist', 'autolink', 'lists', 'link', 'image', 'charmap',
					'anchor', 'searchreplace', 'visualblocks', 'code', 'fullscreen',
					'insertdatetime', 'media', 'table', 'preview', 'help', 'wordcount'
				],
				toolbar: 'undo redo | blocks | ' +
					'bold italic forecolor | alignleft aligncenter ' +
					'alignright alignjustify | bullist numlist outdent indent | ' +
					'removeformat | help',
				content_style: 'body { font-family:' + theme.typography.fontFamily + '; font-size:16px;text-align:left; }'
			}}
		/>
	);
}