import AutoFixHighIcon from '@mui/icons-material/AutoFixHigh';
import { Box, CircularProgress } from '@mui/material';

const AiTextFix = (props) => {
	const { className:customClassName = "", loading = false, onClick=()=>{} } = props;

	return (
		<Box className={customClassName}>
			{loading ? <CircularProgress size={24} sx={{color:"#b2b2b2"}}/> : <AutoFixHighIcon sx={{width:"24px", height:"24px"}} onClick={onClick} />}
		</Box>
	);
}

export default AiTextFix;