import { Box, FormControl, TextField, Typography } from "@mui/material";
import RichTextEditor from "../../components/RichTextEditor/RichTextEditor";
import { LoadingButton } from "@mui/lab";
import styles from "./style.module.css";
import Recorder from "../../components/Recorder/Recorder";
import AiTextFix from "../../components/AiTextFix/AiTextFix";
import { observer } from "mobx-react-lite";

const Document = (props) => {
	const { data = {}, loading = false, sendData = () => { }, aiFixText=()=>{}, onChangeTitle = () => { }, onChangeDescription = () => { } } = props;
	const { id, title, description } = data;

	const pageTitle = !id ? " " : " ";
	const sendBtnName = !id ? " " : " ";

	const handleChangeTitle = (e) => {
		onChangeTitle(e.target.value);
	}

	const handleChangeDescription = (v) => {
		onChangeDescription(v);
	}

	const handleVoiceToText = (v) => {
		const div = document.createElement("div");
		div.innerHTML = description || "";
		div.innerText = div.innerText + v;
		div.remove();

		onChangeDescription(div.innerText );
	}

	const handleAiTextFix = async (v) => {
		await aiFixText(v);
	}

	const handleSendData = () => {
		sendData();
	}

	return (
		<Box>
			<Typography sx={{ mb: 3 }} variant="h5" gutterBottom>{pageTitle}</Typography>
			<FormControl className={styles.form_control}>
				<TextField sx={{ width: "100%" }} label=" " variant="standard" value={title} onChange={handleChangeTitle} />
			</FormControl>
			<Box className={styles.custom_tools}>
				<Recorder className={styles.custom_tool} value={description} setValue={handleVoiceToText} />
				<AiTextFix className={styles.custom_tool} loading={loading} onClick={handleAiTextFix} />
			</Box>
			<FormControl className={styles.form_control}>
				<RichTextEditor initValue={description || null} onEditorChange={handleChangeDescription} />
			</FormControl>
			<LoadingButton className={styles.btnSubmit} variant="contained" type="submit" loading={loading} onClick={handleSendData}>{sendBtnName}</LoadingButton>
		</Box>
	)
}

export default observer(Document);