import { observer } from "mobx-react-lite";
import { Route, Routes } from "react-router";

const AppRoutes = ({routes})=>{
	return (
		<Routes>
			{routes.map((r, i) => (
				<Route
					key={i}
					path={r.path}
					component={r.component}
					element={r.component}
					exact={r.exact}
				/>
			))
			}
		</Routes>
	);
}

export default observer(AppRoutes);