import { action, makeAutoObservable, observable, runInAction } from "mobx";
import { makePersistable } from "mobx-persist-store";
import { users, roles, files } from "../demo";
import DataService from "../services/DocService";

class DataStore {
	isLoading = false;

	users = users;
	roles = roles;
	documents = [];
	files = files;

	constructor() {
		makeAutoObservable(this,{
			isLoading:observable,
			users:observable,
			roles:observable,
			documents:observable,
			files:observable,
			setLoading:action,
			setUsers:action,
			setRoles:action,
			setDocuments:action,
			setFiles:action,
			setDocumentTitle:action,
			setDocumentDescription:action
		});
		makePersistable(this, {
			name: 'DataStore',
			storage: typeof window !== "undefined" ? window.localStorage : undefined,
			properties: [
				'users',
				'roles',
				'documents',
				'files'
			]
		})
	}

	setLoading(loading) {
		this.isLoading = loading;
	}

	setUsers(users) {
		this.users = users;
	}

	setRoles(roles) {
		this.roles = roles;
	}

	setDocuments(documents) {
		this.documents = documents;
	}

	setFiles(files) {
		this.files = files;
	}

	setDocumentTitle(d, v) {
		d.title = v;
	}

	setDocumentDescription(d, v) {
		d.description = v;
	}

	async getDocuments() {
		this.setLoading(true);
		try {
			const respDocuments = await DataService.getDocuments();
			console.log(respDocuments.data)
			runInAction(() => {
				this.setDocuments(respDocuments.data);
			});

		}
		catch (e) {
			console.error(e);

		}
		finally {
			runInAction(() => {
				this.setLoading(false);
			});

		}
	}
}

export default DataStore;