import axios from "axios";
import { $aiApi, $documentApi } from "../http";

class AiService {
	static async fixText(txt) {
		const requestOptions = {
			method: 'POST',
			headers: { 'Access-Control-Allow-Origin': '*', 'content-type': 'application/json' },
			mode: 'no-cors',
			body: JSON.stringify({ "text": "   " })
		};
		return fetch('http://176.123.163.239:100/corrections-api/correct-text-ai-powered', requestOptions);

		return axios({
			withCredentials:true,
			method: 'post',
			url: 'http://176.123.163.239:91/corrections-api/correct-text-ai-powered',
			mode:'no-cors',
			headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin':'*' },
			data: {
				"text": "   ",
				"model_config": {
					"from_attributes": true
				}
			}
		});
	}
}

export default AiService;