
import { Navigate } from 'react-router-dom';
import Auth from '../pages/Auth/Auth';
import AuthorizedSection from '../pages/AuthorizedSection/AuthorizedSection';
import DocumentContainer from '../pages/Document/DocumentContainer';

export const PrivateRoutes = [
	{ code: 'home', title:"", path: '/', component: <AuthorizedSection />, exact: true },
	{ code: 'create_document', title:"", path: '/document', component: <DocumentContainer />, exact: true },
	{ code: 'edit_document', title:"", path: '/document/:id', component: <DocumentContainer />, exact: true },
	{ code: 'other', path: '*', component: <Navigate to="/" />, exact: true },
]

export const PublicRoutes = [
	{ code: 'unauthorized', title:" ", path: '/*', component: <Auth />, exact: true },
];